




















































import Vue from "vue";

export default Vue.extend({
    name: "Activities",
    data() {
        return {
            isLoading: false,
        };
    },
    computed: {
        activities() {
            return this.$store.state.activities.all;
        },
    },
    created() {
        this.loadActivities();
    },
    methods: {
        loadActivities() {
            this.isLoading = true;
            this.$store
                .dispatch("activities/load")
                .catch(() => {
                    this.$snotify.error(
                        "Activities konnten nicht geladen werden"
                    );
                })
                .finally(() => {
                    this.isLoading = false;
                });
        },
    },
});
